"use client";
import React, { useState } from "react";
import Header from "../components/Header";
import TestResultsDisplay from "../components/test-results-display";
import { TestResult } from "./types";

export default function Home() {
  const [url, setUrl] = useState("https://www.revyl.ai/");
  const [isLoading, setIsLoading] = useState(false);
  const [result, setResult] = useState<TestResult | null>(null);

  // Default test profile
  const [testProfile, setTestProfile] = useState({
    name: "Hayden White",
    email: "haydenswhite@gmail.com",
    phone: "+1234567890",
    company: "Example Inc.",
    jobTitle: "Software Engineer",
    country: "United States",
    timezone: "America/New_York",
    description:
      "this is a test profile for my AI agent demo booking test agent-- sorry if this works.",
  });

  // Default goals
  const [goals, setGoals] = useState([
    'Detect "Book a Demo" (or similar) buttons/links',
    `Fill out any required forms with the following profile. Not all fields have have to be used or exist SO MOVE ON IF YOU CAN'T FIND THE FIELD, etc. Also, the order does not correpsond to the order of the site's flow. ${JSON.stringify(
      testProfile
    )}`,
    "Complete the meeting scheduling process",
    "Verify the booking was successful (e.g., confirmation page)",
  ]);

  // Handle test profile changes
  const handleProfileChange = (field: string, value: string) => {
    setTestProfile((prev) => ({
      ...prev,
      [field]: value,
    }));
  };

  // Handle goals changes
  const handleGoalChange = (index: number, value: string) => {
    setGoals((prev) => {
      const newGoals = [...prev];
      newGoals[index] = value;
      return newGoals;
    });
  };

  // Add a new goal
  const addGoal = () => {
    setGoals((prev) => [...prev, ""]);
  };

  // Remove a goal
  const removeGoal = (index: number) => {
    setGoals((prev) => prev.filter((_, i) => i !== index));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);
    setResult(null);

    // Update the second goal with the current test profile
    const updatedGoals = [...goals];
    updatedGoals[1] = `Fill out any required forms with the following profile. Not all fields have have to be used or exist SO MOVE ON IF YOU CAN'T FIND THE FIELD, etc. Also, the order does not correpsond to the order of the site's flow. ${JSON.stringify(
      testProfile
    )}`;

    try {
      const response = await fetch("http://localhost:4000/api/test", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          url,
          testProfile,
          goals: updatedGoals,
        }),
      });

      const data = await response.json();
      setResult({
        status: response.ok ? "success" : "error",
        statusCode: response.status,
        message: data.message || "Test completed",
        details: data,
      });
    } catch (error: unknown) {
      console.error(error);
      setResult({
        status: "error",
        message: "Failed to connect to the server",
      });
    } finally {
      setIsLoading(false);
    }
  };

  const [profileOpen, setProfileOpen] = useState(false);
  const [goalsOpen, setGoalsOpen] = useState(false);

  return (
    <div className="min-h-screen bg-gray-50">
      <Header />
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        <div className="w-3xl mx-auto">
          <div className="bg-white shadow sm:rounded-lg p-6 ">
            <h2 className="text-xl font-semibold text-gray-900 mb-6">
              Test Demo Booking Flow
            </h2>
            <form onSubmit={handleSubmit} className="space-y-6">
              <div>
                <label
                  htmlFor="url"
                  className="block text-sm font-medium text-gray-700"
                >
                  Landing Page URL
                </label>
                <div className="mt-1">
                  <input
                    type="url"
                    name="url"
                    id="url"
                    required
                    className="shadow-sm focus:ring-indigo-500 focus:border-indigo-500 block w-full sm:text-sm border-gray-300 rounded-md p-2 h-8 text-gray-900"
                    placeholder="https://example.com"
                    value={url}
                    onChange={(e) => setUrl(e.target.value)}
                  />
                </div>
                <p className="mt-2 text-sm text-gray-500">
                  Enter the URL of the landing page you want to test
                </p>
              </div>

              {/* Test Configuration Dropdowns */}
              <div className="space-y-4">
                {/* Test Profile Dropdown */}
                <div className="border rounded-md overflow-hidden">
                  <button
                    type="button"
                    onClick={() => setProfileOpen(!profileOpen)}
                    className="flex justify-between items-center w-full p-3 text-sm font-medium text-gray-700 bg-gray-50 hover:bg-gray-100"
                  >
                    Edit Test Profile
                    <span>{profileOpen ? "▲" : "▼"}</span>
                  </button>

                  {profileOpen && (
                    <div className="p-3 border-t">
                      <div className="space-y-3">
                        {Object.entries(testProfile).map(([field, value]) => (
                          <div
                            key={field}
                            className="grid grid-cols-3 gap-2 items-center"
                          >
                            <label className="text-sm text-gray-700 col-span-1 capitalize">
                              {field.replace(/([A-Z])/g, " $1").trim()}:
                            </label>
                            <input
                              type="text"
                              value={value}
                              onChange={(e) =>
                                handleProfileChange(field, e.target.value)
                              }
                              className="col-span-2 shadow-sm focus:ring-indigo-500 focus:border-indigo-500 block w-full sm:text-sm border-gray-300 rounded-md p-1 h-8 text-gray-900"
                            />
                          </div>
                        ))}
                      </div>
                    </div>
                  )}
                </div>

                {/* Test Goals Dropdown */}
                <div className="border rounded-md overflow-hidden">
                  <button
                    type="button"
                    onClick={() => setGoalsOpen(!goalsOpen)}
                    className="flex justify-between items-center w-full p-3 text-sm font-medium text-gray-700 bg-gray-50 hover:bg-gray-100"
                  >
                    Edit Test Goals
                    <span>{goalsOpen ? "▲" : "▼"}</span>
                  </button>

                  {goalsOpen && (
                    <div className="p-3 border-t">
                      <div className="space-y-3">
                        {goals.map((goal, index) => (
                          <div key={index} className="flex items-start gap-2">
                            <textarea
                              value={goal}
                              onChange={(e) =>
                                handleGoalChange(index, e.target.value)
                              }
                              className="flex-grow shadow-sm focus:ring-indigo-500 focus:border-indigo-500 block w-full sm:text-sm border-gray-300 rounded-md p-2 text-gray-900"
                              rows={2}
                            />
                            <button
                              type="button"
                              onClick={() => removeGoal(index)}
                              className="inline-flex items-center p-1 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-red-600 hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500 h-8 w-8 flex-shrink-0 justify-center"
                            >
                              ×
                            </button>
                          </div>
                        ))}
                        <button
                          type="button"
                          onClick={addGoal}
                          className="inline-flex items-center px-3 py-1 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                        >
                          Add Goal
                        </button>
                      </div>
                    </div>
                  )}
                </div>
              </div>

              <div>
                <button
                  type="submit"
                  disabled={isLoading}
                  className={`w-full flex justify-center py-2 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 ${
                    isLoading ? "opacity-75 cursor-not-allowed" : ""
                  }`}
                >
                  {isLoading ? "Testing..." : "Start Test"}
                </button>
              </div>
            </form>
            {/* Result Display */}
            <div className="mt-3">
              {result && <TestResultsDisplay results={result} />}
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}
